.serviceBox {
  color: #333;
  font-family: 'Poppins', sans-serif;
  text-align: right;
  padding: 0 40px 10px 10px;
  position: relative;
  z-index: 1;
}
.serviceBox:before {
  content: '';
  background: linear-gradient(to bottom, #10ac84, #10ac84, transparent);
  height: calc(100% - 50px);
  width: 25px;
  position: absolute;
  right: 0;
  bottom: 0;
  z-index: -1;
}
.serviceBox .service-icon {
  color: #10ac84;
  background-color: #fff;
  font-size: 45px;
  text-align: center;
  line-height: 85px;
  height: 105px;
  width: 105px;
  margin: 0 -40px 10px 0;
  border: 12px solid #10ac84;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.5) inset, 0 0 10px rgba(0, 0, 0, 0.4);
  border-radius: 50%;
  display: inline-block;
}
.serviceBox:hover .service-icon i {
  transform: rotate(360deg);
  transition: all 0.3s;
}
.serviceBox .title {
  color: #10ac84;
  font-size: 19px;
  font-weight: 600;
  text-transform: uppercase;
  text-align: left;
  margin: 0 0 10px;
}
.serviceBox .description {
  font-size: 14px;
  line-height: 24px;
  letter-spacing: 0.5px;
  text-align: justify;
  margin: 0 0 10px;
}
.serviceBox.red:before {
  background: linear-gradient(to bottom, #ee5253, #ee5253, transparent);
}
.serviceBox.red .service-icon {
  color: #ee5253;
  border-color: #ee5253;
}
.serviceBox.red .title {
  color: #ee5253;
}
.serviceBox.blue:before {
  background: linear-gradient(to bottom, #2e86de, #2e86de, transparent);
}
.serviceBox.blue .service-icon {
  color: #2e86de;
  border-color: #2e86de;
}
.serviceBox.blue .title {
  color: #2e86de;
}
.serviceBox.purple:before {
