  border: none;
  border-bottom: 4px solid #34495e;
}
.vertical-tab .tab-content {
  color: #000;
  background-color: #dfe4e9;
  font-size: 14px;
  letter-spacing: 1px;
  line-height: 23px;
  padding: 15px;
  border-radius: 16px 55px;
  display: table-cell;
  position: relative;
}
.vertical-tab .tab-content h3 {
  font-weight: 600;
  text-transform: uppercase;
  margin: 0 0 5px 0;
}
@media only screen and (max-width: 479px) {
  .vertical-tab .nav-tabs {
    display: block;
    width: 100%;
    border-right: none;
  }
  .vertical-tab .nav-tabs li a {
    margin: 0 0 10px;
  }
  .vertical-tab .tab-content {
    display: block;
    padding: 20px 15px 10px;
    border-radius: 10px 45px;
  }
  .vertical-tab .tab-content h3 {
    font-size: 18px;
  }
}
.pulse-footer {
